import {Injectable} from '@angular/core';
// @ts-ignore
import {Command} from '../models/command.model';
import {HttpClient} from '@angular/common/http';
import {BehaviorSubject, Observable,} from 'rxjs';
import {concatMap, filter} from 'rxjs/operators';

//const baseUrl = 'http://192.168.0.246:8080/api/client/commands';
const baseUrl = 'https://oafish-orange-production.up.railway.app/api/client/commands';

@Injectable({
  providedIn: 'root',
})
export class CommandClientService {
  constructor(private http: HttpClient) {
  }

  getAll(): Observable<Command[]> {
    return this.http.get<Command[]>(baseUrl);
  }

  getCommandsById(id: number): Observable<Command> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${id}`);
  }

  delete(id: any): Observable<any> {
    return this.http.delete(`${baseUrl}/${id}`);
  }

  create(data: any): Observable<any> {
    return this.http.post(baseUrl, data);
  }

  update(id: any, data: any): Observable<any> {
    return this.http.put(`${baseUrl}/${id}`, data);
  }

  getElementsAjoutesAujourdhui(): Observable<Command[]> {
    return this.http.get<Command[]>(`${baseUrl}/added-today`);
  }

  getElementsAjoutesHier(): Observable<Command[]> {
    return this.http.get<Command[]>(`${baseUrl}/added-yesterday`);
  }

  getElementsAjoutesCetteSemaine(): Observable<Command[]> {
    return this.http.get<Command[]>(`${baseUrl}/added-this-week`);
  }

  getElementsAjoutesCeMois(): Observable<Command[]> {
    return this.http.get<Command[]>(`${baseUrl}/added-this-mounth`);
  }

  getElementsAjoutesTotal(): Observable<Command[]> {
    return this.http.get<Command[]>(`${baseUrl}/getAll`);
  }

  getElementsAjoutesOrdreDecroissant(): Observable<Command[]> {
    return this.http.get<Command[]>(`${baseUrl}/ordre-inverse`);
  }

  getSommePrixAujourdhui(): Observable<number> {
    return this.http.get<number>(`${baseUrl}/sum-price-today`);
  }

  getSommePrixHier(): Observable<number> {
    return this.http.get<number>(`${baseUrl}/sum-price-yesterday`);
  }

  getSommePrixCetteSemaine(): Observable<number> {
    return this.http.get<number>(`${baseUrl}/sum-price-thisweek`);
  }

  getSommePrixCeMois(): Observable<number> {
    return this.http.get<number>(`${baseUrl}/sum-price-thismounth`);
  }

  getSommePrixTotal(): Observable<number> {
    return this.http.get<number>(`${baseUrl}/somme-prix-total`);
  }
}
